.app {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-top: 10rem;
}
.app .count {
  margin-bottom: 2rem;
  text-align: center;
  display: flex;
  margin-left: 10rem;
}
.app .count h3 {
  line-height: 8.6rem;
  margin-right: 1rem;
}
.app .count h1 {
  font-size:5em;
  margin-top: 2rem;
}
.app .buttons button {
  width: 10rem;
  height: 10rem;
  border: none;
  margin: 0 2rem;
  font-size: 5em;
  cursor: pointer;
  background-color: rgb(157, 211, 202);
}
.app .buttons button:hover{
  font-size: 5em;
}
.app .buttons button:focus{
  outline: 0;
}